body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto","Oxygen", 
        "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
        monospace;
}

.spinner {
    -webkit-animation: img-spin infinite 15s linear;
            animation: img-spin infinite 15s linear;
}

@-webkit-keyframes img-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}

@keyframes img-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg)
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg)
    }
}

.dayDiv {
    white-space: nowrap;
    overflow: auto;
}

.onedayDiv {
    display: inline-block;
    padding-right: 10px;
    padding-left:10px;
}

    .onedayDiv:not(:first-child) {
        padding-left: 5px;
        border-left: dashed 1px black;
    }

    .onedayDiv > p {
        margin: 0;
    }

    .onedayDiv > img {
        width: 30px;
    }

#forcast-main {
    position: relative;
    background-color: white;
    overflow: auto;
    border-radius: 25px;
    text-align: center;
    padding: 10px;
    margin:10px;
}

    #forcast-main > h2 {
        margin: 0;
        border-bottom: solid 1px black;
    }

#fivedaysdiv {
    white-space: nowrap;
    overflow: auto;
   padding:5px;
}
.fivedayOne {
    display: inline-block;
    padding-left: 10px;
    padding-right: 10px;
    margin-top:5px;
    margin-left:5px;
    
}
.fivedayOne:hover{
    cursor:pointer;
}
.fivedayOne > p{
    margin:5px;
}

#otherCurrentdata > p {
    font-size: 15px;
    display: inline-block;
    padding: 0;
    margin: 0;
    padding-right: 5px;
    padding-left: 5px;
}

    #otherCurrentdata > p:not(:first-child) {
        border-left: solid 1px;
    }

#current-div {
    position: relative;
    text-align: center;
}
    #current-div > p{
        margin: 0;
        padding:0;
        display: inline-block;
    }

    #current-div > h1 {
        font-size: 70px;
        font-weight: lighter;
    }
    #current-div > h2{
        font-size:45px;
    }
    #current-div > h2, h1 {
        width: 100%;
        text-align: center;
        margin-top: 0;
        margin-bottom: 0;
    }
    #current-div > img {
        width: 100px;
    }
html, body {
    width: 100%;
    height: 100%;
}

#app {
    position: absolute;
    width: 50%;
    left: 50%;
    height: 100%;
    overflow: auto;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    background-color: cornflowerblue;
    /* background-image: url(https://jooinn.com/images/storm-clouds-44.jpg)*/
}

p, td, h1, h2 {
    color: black;
}

@media only screen and (max-width: 800px) {
    #app{
        position:absolute;
        width:100%;
    }
}
